import request from '@/common/js/fetch'

export function myBankCards() {
  return request({
    url: '/bankCard/myBankCards',
    method: 'get'
  })
}

export function deleteCard(params) {
    return request({
      url: '/bankCard/delete',
      method: 'get',
      params
    })
  }


  export function addCard(params) {
    return request({
      url: '/bankCard/addCard',
      method: 'post',
      params
    })
  }


